import styled from 'styled-components';


const TITLE2 = styled.h1`
	color: white;
	font-family: 'Quattrocento', serif;
	font-size: 400%;
	letter-spacing:.5px;
	margin: 6rem 0 0;
	line-height: 0.8;
	margin-bottom:10px;
`;


export default TITLE2;


